.digital-clock {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  font-family: monospace;
  height: 100vh;
}

.digital-row {
  display: flex;
  flex-direction: row;
}

.digital-digit {
  width: 30px;
  height: 30px;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.1rem;
}

.colon {
  font-size: 2rem;
  margin: 0.5rem;
}
